.tos-container {
  position: relative;
  z-index: 3;
  cursor: pointer;
  padding: 10px;
  background-image: linear-gradient(#ffffff, #ffffff);
  box-shadow: 0 20px 15px 0 rgba(70, 115, 122, 0.15); }
  @media screen and (min-width: 992px) {
    .tos-container {
      margin: 0 20px; } }
  .tos-container .nav-link {
    color: #354F53;
    font-weight: 800;
    border: none; }
  .tos-container .arrow {
    transform-origin: 50%;
    transition: 0.2s ease-in-out;
    width: 25px; }
    @media screen and (min-width: 820px) {
      .tos-container .arrow {
        width: 35px; } }
  .tos-container .turn {
    transform-origin: 50%;
    -ms-transform: rotate(180deg) !important;
    -webkit-transform: rotate(180deg) !important;
    transform: rotate(180deg) !important;
    transition: 0.2s ease-in;
    width: 25px; }
    @media screen and (min-width: 820px) {
      .tos-container .turn {
        width: 35px; } }

.tos-inner {
  position: relative;
  z-index: 3; }
  @media screen and (min-width: 992px) {
    .tos-inner {
      margin-left: 20px;
      margin-right: 20px; } }
  .tos-inner .card {
    border-radius: 0; }
    .tos-inner .card .card-body {
      padding: 10px; }
      @media screen and (min-width: 820px) {
        .tos-inner .card .card-body {
          padding: 1.25rem; } }
  .tos-inner ol {
    padding-left: 20px;
    color: #46737A;
    font-size: 14px; }
    .tos-inner ol li {
      padding: 5px; }
