.referral-info .content .referral-code {
  padding: 9px 9px 13px 9px;
  margin-top: 22px;
  background: white;
  /* Mild shadow */
  box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.05);
  border-radius: 20px;
  text-align: center; }

.referral-info .content .unique {
  font-weight: bold;
  font-size: 10px;
  color: #00D7AF; }

.referral-info .content .referral-desc {
  font-weight: 600;
  font-size: 12px;
  color: #B6BDCB;
  text-align: center; }

.referral-info .content .code {
  line-height: 1;
  font-weight: bold;
  font-size: 30px;
  color: #6E7F95; }

.referral-info .content .sign-in {
  padding: 20px 0; }
  .referral-info .content .sign-in b {
    color: #00D7AF; }
  .referral-info .content .sign-in .code {
    font-size: 14px; }
  .referral-info .content .sign-in button:hover b {
    color: #36B29B; }

.referral-info .how-container {
  display: flex;
  flex-direction: column;
  align-items: center; }
  .referral-info .how-container img {
    width: 100%;
    max-width: 310px;
    height: auto; }
  .referral-info .how-container h3,
  .referral-info .how-container h5 {
    max-width: 280px; }
  .referral-info .how-container h3 {
    text-align: center;
    margin-bottom: 20px; }
